import './imports.js';
import Header from '../components/header';
import NavBar from '../components/navBar';
import NewList from '../components/newsList'
import { NEWS_TYPE } from '../data';
import services from '../services'
import Loading from '../components/loading'
import { touchBottom } from '../libs/utils'
import LoadMore from '../components/loadMore'

  ; import loadMore from '../components/loadMore';
(function (doc) {
  const oApp = doc.querySelector('#app');
  const config = {
    type: 'top',
    count: 10,
    pageNum: 0,
    isLoading: true
  }
  const NewsData = {}
  let oNewsWrapper = null
  let t = null
  async function init () {
    render();
    config.isLoading = false
    await getNewlist()
    bindEvent();
  }
  function render () {
    const header = Header.tpl({
      url: '/',
      showLeftIcon: false,
      showRightIcon: true,
      title: '新闻头条',
    })
    const navBar = NavBar.tpl({
      data: NEWS_TYPE
    })
    const newsList = NewList.WrapperTpl(8.2)
    oApp.innerHTML += (header + navBar + newsList);
    oNewsWrapper = doc.querySelector('.news-wrapper')
  }
  function bindEvent () {
    NavBar.bindEvent(setType)
    window.addEventListener('scroll', touchBottom.bind(null, reachBottom), false)
  }
  function reachBottom () {
    const { isLoading, pageNum, type } = config
    if (!isLoading) {
      config.isLoading = true
      setTimeout(() => {
        config.isLoading = false
      }, 3000)
      if (pageNum >= NewsData[type].length) {
        loadMore.add(oNewsWrapper, false)
      } else {
        config.pageNum += 1;
        loadMore.add(oNewsWrapper, true)
        clearTimeout(t)
        t = setTimeout(() => {
          renderNewList(NewsData[type], pageNum, type)
          loadMore.remove()
        }, 3000)
      }
    }

  }
  function renderNewList (data, pageNum, type) {
    oNewsWrapper.innerHTML += NewList.tpl({
      data,
      pageNum,
      type
    })
    NewList.imgShow()
  }
  async function getNewlist () {
    const { type, count, pageNum } = config
    if (NewsData[type]) {
      console.log('poor')
      oNewsWrapper.innerHTML = ''
      renderNewList(NewsData[type], pageNum, type)
      return;
    }
    oNewsWrapper.innerHTML = Loading.tpl()
    NewsData[type] = await services.getNewList(type, count)
    setTimeout(() => {
      oNewsWrapper.innerHTML = ''
      renderNewList(NewsData[type], pageNum, type)
    }, 1000)
  }
  function setType (type) {
    config.type = type
    config.pageNum = 0
    oNewsWrapper.innerHTML = ''
    clearTimeout(t)
    getNewlist()
  }
  init();
})(document);
