import tpl0 from './tpl/tpl.tpl'
import tpl1 from './tpl/tpl1.tpl'
import tpl2 from './tpl/tpl2.tpl'
import tpl3 from './tpl/tpl3.tpl'
import WrapperList from './tpl/wrapperList.tpl'
import { compileTemplate } from '../../libs/utils'
import './index.scss'

export default {
  name: 'NewList',
  WrapperTpl (top) {
    return compileTemplate(WrapperList, {
      top
    })
  },
  tpl (options) {
    const { data, pageNum } = options

    let tpl = ''
    let res = '';
    data[pageNum].map((item, index) => {
      const { title, author_name, category, date, thumbnail_pic_s, thumbnail_pic_s02, thumbnail_pic_s03, uniquekey, url } = item
      if (!item.thumbnail_pic_s) {
        tpl = tpl0
      } else if (thumbnail_pic_s && !thumbnail_pic_s02) {
        tpl = tpl1
      } else if (thumbnail_pic_s02 && !thumbnail_pic_s03) {
        tpl = tpl2
      } else if (thumbnail_pic_s02 && thumbnail_pic_s03) {
        tpl = tpl3
      }
      res += compileTemplate(tpl, {
        author_name,
        category,
        date,
        title,
        thumbnail_pic_s,
        thumbnail_pic_s03,
        thumbnail_pic_s02,
        uniquekey,
        url,
        pageNum,
        index
      })
    })
    return res
  },
  imgShow () {
    const imgs = document.querySelectorAll('img')
      ;[...imgs].map(item => {
        item.onload = () => {
          item.style.opacity = '1'
        }
      })
  }
}